import axios from 'axios'
// axios.defaults.headers.post['Content-Type'] = 'application/x-www-urlencoded'
const http = axios.create()

http.defaults.timeout = 3000

http.interceptors.request.use(config => { //  // 
    // do sth
    return config
}, error => {
    console.log(error)
    return Promise.reject(error)
})

http.interceptors.response.use(response => { //  // 
    // do something
    return response
}, error => {
    console.log(error)
    return Promise.reject(error)
})

export function fetch(url, params) { // axiospost
    return new Promise((resolve, reject) => { // promise ,
        axios.post(url, params).then(response => {
            resolve(response.data) // promise
        }).catch(error => {
            reject(error) // promise
        })
    })
}

export default { // htto_mock
    http_mock(url, params) {
        return fetch(url, params)
    }
}


